.button {
  /* Calculated properties */
  line-height: 1.5;
  padding-top: 0.5625em;
  padding-bottom: 0.5625em;
  padding-right: 1.5em;
  padding-left: 1.5em;
  border-width: 0;
  box-sizing: border-box;
  font-size: inherit;
  font-family: inherit;
  display: inline-block;
  margin: 0;
  vertical-align: baseline;
  appearance: none;
  text-decoration: none;
  cursor: pointer;
  position: relative;
  text-align: center;
  color: #000;
  font-weight: 600;
  background: #FBC138;
  border-radius: 100px;
}
.has-mouse .button {
  transition: transform 0.1s ease-in-out, opacity 0.2s ease-out;
}
.button--alt {
  border: 1px solid #FBC138;
  background: #FFF;
}

.button--trans {
  background: transparent;
  color: #FFF;
  border: 1px solid #FBC138;
  padding-top: calc(0.5625em - 1px);
  padding-bottom: calc(0.5625em - 1px);
}
.button--trans.link--disabled:after {
  color: #FFF;
}

.link--disabled {
  color: rgba(0, 0, 0, 0);
}
.link--disabled:after {
  position: absolute;
  content: "Please wait...";
  top: 50%;
  left: 50%;
  color: #000;
  font-size: 1.2rem;
  width: 100%;
  transform: translateY(-50%) translateX(-50%);
  animation-name: fadeIn;
  animation-duration: 0.2s;
  text-align: center;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.loader:before,
.loader:after,
.loader {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: load7 1.8s infinite ease-in-out;
  animation: load7 1.8s infinite ease-in-out;
}

.loader {
  color: #FFF;
  font-size: 10px;
  margin: 0 auto;
  position: relative;
  text-indent: -9999em;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

.loader:before {
  left: -3.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.loader:after {
  left: 3.5em;
}

.loader:before,
.loader:after {
  content: "";
  position: absolute;
  top: 0;
}

@-webkit-keyframes load7 {
  0%, 80%, 100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
@keyframes load7 {
  0%, 80%, 100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
